import { useState } from 'react';
import { useForm, Controller } from 'react-hook-form';
import styles from './Courier.module.css';
import AddressNumber from '../../UI/AddressNumber/AddressNumber';
import TextField from '../../UI/TextField/TextField';
import Checkbox from '../../UI/Checkbox/Checkbox';
// import NameInput from '../../UI/NameInput/NameInput';
import Address from '../../UI/AddressInput/AddressInput';

function Courier() {
	const [apartament, setApartament] = useState('');
	const handleApartamentInput = (e) => {
		setApartament(e.target.value);
	};

	const [entrance, setEntrance] = useState('');
	const handleEntranceInput = (e) => {
		setEntrance(e.target.value);
	};

	const [floor, setFloor] = useState('');
	const handleFloorInput = (e) => {
		setFloor(e.target.value);
	};

	const [comment, setComment] = useState('');
	const handleCommentInput = (e) => {
		setComment(e.target.value);
	};

	const [checked, setChecked] = useState(false);

	const changeCheckbox = () => {
		setChecked(!checked);
	};
	const {
		control,
		handleSubmit,
		reset,
		formState: { errors },
	} = useForm({
		mode: 'onChange',
		defaultValues: {
			address: '',
		},
	});

	const onSubmit = () => {
		reset();
	};

	return (
		<section className={styles.address}>
			<p className={styles.text}> </p>
			<form onSubmit={handleSubmit(onSubmit)}>
				<Controller
					name="address"
					control={control}
					rules={{
						required: ' ',
						minLength: {
							value: 5,
							message: '    6 ',
						},
						pattern: {
							value:
								/[0-9]{6},\s[--a-zA-Z-,.\s\d]+,\s[--a-zA-Z-,.\s\d]+,\s[--a-zA-Z-,.\s\d]+(,\s[--a-zA-Z-,.\s\d])?/gi,
							message: ' git ',
						},
					}}
					render={({ field: { onBlur, onChange, value } }) => (
						<Address
							onBlur={onBlur}
							onChange={onChange}
							value={value}
							inputId="address"
							label=" , , "
							helperText={errors.address?.message?.toString()}
							error={!!errors.address?.message}
						/>
					)}
				/>
				<div className={styles.addressNumbers}>
					<AddressNumber
						onChange={handleApartamentInput}
						value={apartament}
						place=""
					/>
					<AddressNumber
						onChange={handleEntranceInput}
						value={entrance}
						place=""
					/>
					<AddressNumber
						onChange={handleFloorInput}
						value={floor}
						place=""
					/>
				</div>
				<div className={styles.comment}>
					<TextField
						onChange={handleCommentInput}
						value={comment}
						label="  "
					/>
				</div>
			</form>
			<Checkbox
				onChange={changeCheckbox}
				label=" "
				checked={checked}
			/>
		</section>
	);
}
export default Courier;
