import { useForm, Controller } from 'react-hook-form';
import styles from './RecipientForm.module.css';
import ContainerForms from '../ContainerForms/ContainerForms';
import NameInput from '../../UI/NameInput/NameInput';

function RecipientForm() {
	const {
		control,
		handleSubmit,
		reset,
		formState: { errors },
	} = useForm({
		mode: 'onChange',
		defaultValues: {
			firstName: '',
			surname: '',
			telephone: '',
			email: '',
		},
	});

	const onSubmit = (data) => {
		// eslint-disable-next-line
		console.log(data);
		reset();
	};

	return (
		<ContainerForms>
			<div className={styles.formContainer}>
				<span></span>
				<form className={styles.form} onSubmit={handleSubmit(onSubmit)}>
					<Controller
						control={control}
						rules={{
							required: ' ',
							minLength: {
								value: 2,
								message: '    1 ',
							},
							pattern: {
								value: /^(?=.{1,29}$)[--a-zA-Z\s-]*$/gi,
								message:
									' : , , , ',
							},
						}}
						render={({ field: { onChange, onBlur, value } }) => (
							<NameInput
								onBlur={onBlur}
								onChange={onChange}
								value={value}
								inputId="firstName"
								label=""
								helperText={errors.firstName?.message?.toString()}
								error={!!errors.firstName?.message}
							/>
						)}
						name="firstName"
					/>
					<Controller
						control={control}
						rules={{
							required: ' ',
							minLength: {
								value: 2,
								message: '    1 ',
							},
							pattern: {
								value: /^(?=.{1,29}$)[--a-zA-Z\s-]*$/gi,
								message:
									' : , , , ',
							},
						}}
						render={({ field: { onChange, onBlur, value } }) => (
							<NameInput
								onBlur={onBlur}
								onChange={onChange}
								value={value}
								inputId="surname"
								label=""
								helperText={errors.surname?.message?.toString()}
								error={!!errors.surname?.message}
							/>
						)}
						name="surname"
					/>
					<Controller
						control={control}
						rules={{
							required: ' ',
							minLength: {
								value: 6,
								message: '  ',
							},
							pattern: {
								value: /^[+]?[0-9]+/g,
								message: '  ',
							},
						}}
						render={({ field: { onChange, onBlur, value } }) => (
							<NameInput
								onBlur={onBlur}
								onChange={onChange}
								value={value}
								inputId="telephone"
								label=""
								helperText={errors.telephone?.message?.toString()}
								error={!!errors.telephone?.message}
							/>
						)}
						name="telephone"
					/>
					<Controller
						control={control}
						rules={{
							required: ' ',
							pattern: {
								value: /\S+@\S+\.\S+/gi,
								message: '  email',
							},
						}}
						render={({ field: { onChange, onBlur, value } }) => (
							<NameInput
								onBlur={onBlur}
								onChange={onChange}
								value={value}
								inputId="emailInput"
								label=" "
								helperText={errors.email?.message?.toString()}
								error={!!errors.email?.message}
							/>
						)}
						name="email"
					/>
				</form>
			</div>
		</ContainerForms>
	);
}

export default RecipientForm;
